import config from './config.js'
export default {
	props: {
		imgSrc: {
			type: String
		},
		mode:{
			type:String,
			default:'aspectFill'
		}
	},
	data() {
		return {
			loadingSrc:config.loadingPic,//加载loading或报错图片
			loadingShow: true, //加载动画
			loadingTime: .6, //图片加载时间
			isError:false,
		}
	},
	methods: {
		loaded(e) {
			this.loadingShow = false;
		},
		errImg(e){
			this.isError=true;
			this.loadingSrc=config.noFoundPic
		}
	}
}
